<template>
    <div>
        <el-table
                :data="formList"
                style="width: 100%"
                v-loading="loading">
            <el-table-column
                    prop="id"
                    label="表单ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="表单名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="userId"
                    label="创建者用户ID">
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100">
                <template slot-scope="scope">
                    <el-button @click="handleEdit(scope.row.id)" type="text" size="small">编辑</el-button>
                    <el-button @click="handleDelete(scope.row.id)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
  import { formAPI } from "../../globalAPI";
  export default {
    name: "index",
    data() {
      return {
        formList: [],
        loading: false
      }
    },
    methods: {
      getFormList() {
          this.loading = true;
          this.axios.post(formAPI.getFormListByUser)
              .then((response) => {
                  this.formList = response.data.objData;
                  this.loading = false;
              })
              .catch((error) => {
                  console.log(error);
                  this.loading = false;
              })
      },
      handleEdit(id) {
        this.$router.push({
          name: "FormBuilder",
          params: {
            formId: id
          }
        })
      },
      handleDelete(id) {
          this.$confirm('此操作将永久删除该表单, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
          }).then(() => {
              // 点击确定执行的操作
              let data = Array.of(id);
              this.axios.post(formAPI.deleteForm, this.qs.stringify(
                  {
                      formId: data
                  },
                  { indices: false }
              ))
                      .then((response) => {
                        if(response.data.resultCode === 1){
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.getFormList();
                        }
                      })
                      .catch((error) => {
                          this.$message({
                              type: "error",
                              message: error.response.data.message
                            });
                      });
          }).catch(() => {
              // 点击取消执行的操作
          });
      }
    },
    created() {
      this.getFormList();
    }
  };
</script>

<style scoped>

</style>
